import React from 'react'
import { Card , Button , Row ,Col ,Icon, Input, AutoComplete,} from 'antd';
import { NavLink } from 'react-router-dom'
import './manage.css'


export  default class Manage extends React.Component {


    render() {
        return(
            <div className="homepage">
                <Card title="选择题" className="iquestion">
                    <Row>
                        <Col span="18">
                            <p>选择题。。。。。</p>
                        </Col>
                        <Col span="3">
                            <div className="ibutton1">
                                <NavLink to="./questions">
                                    <Button icon="edit" type="primary" >
                                    更改
                                    </Button>
                                </NavLink>
                            </div>
                        </Col>
                        <Col span="3">
                            <div className="ibutton2">
                                <Button icon="delete" type="danger">
                                删除
                                </Button>
                            </div>
                        </Col>
                    </Row>
                </Card>
            </div>
        )
    }

}